    export default {
        name: "order",
	    
	    props: {
            order_list: Array,
            sign: String,
		    theme: String,
            total_price: Number,
	    },
	    
        filters: {
            getPicUrl(data) {
                let goods_attr = JSON.parse(data).goods_attr;
                return goods_attr.pic_url ? goods_attr.pic_url : goods_attr.cover_pic;
            }
        }
    }
